.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	/* flex-direction: column;
    align-items: center;
    justify-content: center; */
	font-size: calc(10px + 2vmin);
	color: white;
}

.drag-n-drop {
	padding: 0.5rem;
	display: grid;
	gap: 0.5rem;
	width: 100%;
	height: 100%;
	grid-template-columns: repeat(auto-fill, 300px);
	align-items: start;
}
.dnd-group {
	background-color: #49505e;
	border-radius: 5px;
	padding: 0.5rem;
}
.dnd-item {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 150px;
	background-color: white;
	border-radius: 5px;
	color: #282c34;
	font-weight: bold;
}
.dnd-item:not(:last-of-type) {
	margin-bottom: 0.5rem;
}
.dnd-item * {
	margin: 0;
	font-size: 1.2rem;
}
.group-title {
	text-align: left;
	margin-bottom: 0.5rem;
	font-size: 1.5rem;
}
.current {
	background-color: #282c34;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
 